<template>
<div>

    <app-header />

    <div signin-page>
        <section>
            <div class="row">
                <div class="col-md-6 write-up">
                    <h3>A convienent and affordable way to <br> get things done.</h3>
                    <p class="choose-igofer">Choose from a pool of iGofer trained and verified professionals to <br> get
                        your job done </p>
                </div>
                <div class="col-md-4 signin-details">
                    <h5>Sign In</h5>
                    <form @submit.prevent="signIn">
                        <div v-if="!msg">{{msg}}</div>
                        <div class="form-group" :class="{'invalid': $v.signInDetails.email.$error}">
                            <label for="email" style="margin: left">Email address</label>
                            <input type="email" class="form-control" id="email" autofocus aria-describedby="emailHelp"
                                v-model="signInDetails.email">
                            <div v-if="$v.signInDetails.email.$error">
                                <p class="error" v-if="!$v.signInDetails.email.required">E-mail is required</p>
                            </div>
                        </div>
                        <div class="form-group" :class="{'invalid': $v.signInDetails.password.$error}">
                            <label for="password">Password</label>
                            <input type="password" class="form-control" id="password" v-model="signInDetails.password">
                            <div v-if="$v.signInDetails.password.$error">
                                <p class="error" v-if="!$v.signInDetails.password.required">
                                    Password is required
                                </p>
                            </div>
                        </div>
                        <!-- <div class="form-check">
                            <input type="checkbox" class="form-check-input" id="exampleCheck1">
                            <label class="form-check-label" for="exampleCheck1">Check me out</label>
                        </div> -->
                        <button type="submit" class="btn btn-primary mb-3" @click.prevent="signIn">Sign In</button>
                        <span :class="loader ? 'spinner-border': ''"></span>
                        <div v-if="errors">
                                <div v-for="(error, i) in errors" :key="i">
                                    <span v-text="error.msg"></span>
                                </div>
                            </div>
                        <p>Forgot Password?</P>
                        <p>Don't have an account? <span class="btn" @click="User">Sign Up</span></p>
                    </form>
                </div>
            </div>
        </section>
    </div>
</div>

</template>

<script>
import axios from 'axios'
import Header from './Shared/Header.vue';
import {
    required,
    email
} from 'vuelidate/lib/validators'
export default {
    data() {
        return {
            submitStatus: null,
            disabled: false,
            loader: false,
            msg: "",
            errors: {},
            signInDetails: {
                email: '',
                password: ''

            }
        }
    },

    validations: {
        signInDetails: {
            email: {
                required,
                email
            },

            password: {
                required,
            }
        }

    },

    components: {
        'app-header': Header
    },

    methods: {
        // signUp(){
        //    this.$router.push({ path: 'SignUp' })
        // }

        User() {
            this.$router.push({
                path: 'User'
            })
        },

        signIn() {

            this.$v.$touch()
            if (this.$v.$invalid) {
                this.submitStatus = 'ERROR'
            } else {
                this.disabled = true;
                this.loader = true;

                axios.post("/users/login", this.signInDetails)
                    .then(response => {
                        this.User();
                        //console.log("hello world")
                    })
                    .catch(function (error) {
                        console.log(error.response)
                        this.msg = error.response.data.msg
                        // handle error
                        console.log(error.response.data.errors || error.response.data.msg);
                    })
            }
        }

    }
}
</script>

<style scoped src="../assets/styles/SignIn.css"></style>