import SignIn from './components/SignIn.vue';
import HomeContent from './components/HomeContent.vue';
import SignUp from './components/SignUp.vue';
import Hire from './components/Hire.vue';
import UserProfile from './components/Account/User/UserProfile.vue'
import UserHome from './components/Account/User/UserHome.vue'
// import UserAccount from './components/UserAccount.vue';

export const routes = [{
        path: '',
        name: 'Home Component',
        component: HomeContent
    },
    {
        path: '/SignIn',
        name: 'Sign In',
        component: SignIn
    },
    {
        path: '/SignUp',
        name: 'Sign Up',
        component: SignUp
    },
    {
        path: '/Hire',
        name: 'Hire A Worker',
        component: Hire
    },
    {
        path: '/Profile',
        name: 'User Profile',
        component: UserProfile
    },
    {
        path: '/User',
        name: 'User Home',
        component: UserHome
    }

    // {path: '/UserAccount', component: UserAccount}
];