<template>
<div>
    <app-header />

     <div signup-page>
        <section>
            <div class="row">
                <div class="col-md-5 write-up">
                    <h3>A convienent and affordable way to <br> get things done.</h3>
                    <p class="choose-igofer">Choose from a pool of iGofer trained and verified professionals to <br> get
                        your job done </p>
                </div>
                <div class="col-md-5 signup-details">
                    <h5>Create a free account</h5>
                    <p>Welcome, let's get your account set up</p>

                    <div class="error" v-if="submitStatus === 'ERROR'">Please fill the form correctly.</div>

                    <form @submit.prevent="createUser">
                        <div class="form-group" :class="{'invalid': $v.userData.name.$error}">
                            <label for="full-name">Full name</label>
                            <input type="text" class="form-control form-control-sm" id="full-name"
                                aria-describedby="fullnameHelp" v-model.trim="$v.userData.name.$model">

                            <p class="error" v-if="!$v.userData.name.minLength">Name must have at least
                                {{$v.userData.name.$params.minLength.min}} letters.</p>

                            <div v-if="$v.userData.name.$error">
                                <p class="error" v-if="!$v.userData.name.required">Name is required</p>
                            </div>
                        </div>

                        <div class="form-group" :class="{'invalid': $v.userData.email.$error}">
                            <label for="email">Email address</label>
                            <input type="email" class="form-control form-control-sm" id="email"
                                aria-describedby="emailHelp" v-model.trim="$v.userData.email.$model">

                            <p class="error" v-if="!$v.userData.email.email">Please enter a valid E-mail address</p>

                            <div v-if="$v.userData.email.$error">
                                <p class="error" v-if="!$v.userData.email.required">E-mail is required</p>
                            </div>
                        </div>

                        <div class="form-group" :class="{'invalid': $v.userData.phone_number.$error}">
                            <label for="phone-number">Phone Number</label>
                            <input type="tel" class="form-control form-control-sm" id="phone-number"
                                v-model.trim="$v.userData.phone_number.$model">
                            <p class="error" v-if="!$v.userData.phone_number.maxLength">Phone number should not be less
                                than
                                {{$v.userData.phone_number.params.maxLength.min}} characters</p>

                            <div v-if="$v.userData.phone_number.$error">
                                <p class="error" v-if="!$v.userData.phone_number.required">Phone number is required</p>
                            </div>
                        </div>

                        <div class="form-group" :class="{'invalid': $v.userData.password.$error}">
                            <label for="password">Password</label>
                            <input type="password" class="form-control form-control-sm" id="password"
                                v-model.trim="$v.userData.password.$model">

                            <div v-if="$v.userData.password.$error.strongPassword">
                                <p class="error" v-if="!$v.userData.password.strongPassword">
                                    Password must have 1 lowercase, 1 uppercase, 1 digit and 8 characters long
                                </p>
                            </div>

                            <div v-if="$v.userData.password.$error">
                                <p class="error" v-if="!$v.userData.password.required">Password is required</p>
                            </div>
                        </div>

                        <div class="form-group" :class="{'invalid': $v.userData.confirm_password.$error}">
                            <label for="confirm-password">Confirm Password</label>
                            <input type="password" class="form-control form-control-sm" id="confirm-password"
                                v-model="$v.userData.confirm_password.$model">
                            <p class="error" v-if="!$v.userData.confirm_password.sameAsPassword">
                                Passwords must be identical
                            </p>

                            <div v-if="$v.userData.confirm_password.$error">
                                <p class="error" v-if="!$v.userData.confirm_password.required">Password confirmation is
                                    required</p>
                            </div>
                        </div>

                        <!-- put a tick box showing you agree to our terms and condtitions -->

                        <!-- <div class="form-check">
                            <input type="checkbox" class="form-check-input" id="exampleCheck1">
                            <label class="form-check-label" for="exampleCheck1">Check me out</label>
                        </div> -->

                        <button type="submit" v-if="!loader" class="btn btn-primary mb-3" :disabled="disabled">Create
                            Account</button>
                        <span :class="loader ? 'spinner-border': ''"></span>
                        <div v-if="errors">
                            <div v-for="(error, i) in errors" :key="i">
                                <span v-text="error.msg"></span>
                            </div>
                        </div>

                        <!-- <div class="error" v-if="submitStatus === 'OK'">Thanks for your submission!</div> -->
                        <!-- <div class="error" v-if="submitStatus === 'PENDING'">Sending...</div> -->

                        <!-- <button type="submit" class="btn btn-primary mb-3">Create
                            Account</button> -->
                        <p>Already have an account? <span class="btn" @click="signIn">Sign In</span></p>
                        <p>By signing up you agree to our<span> Terms of Service</span></p>
                    </form>
                </div>
            </div>
        </section>
    </div>

</div>

   
</template>

<script>
import axios from 'axios'
import Header from './Shared/Header.vue';
import {required, minLength, maxLength, email, numeric, sameAs} from 'vuelidate/lib/validators'
//import axios from 'axios'
//const touchMap = new WeakMap()
export default {
    //name:   'formValidation',
    data() {
        return {
            disabled: false,
            submitStatus: null,
            loader: false,
            errors: {},
            userData: {
                name: '',
                email: '',
                phone_number: '',
                password: '',
                confirm_password: '',
                // termsAndCondition: false,
            }
        }
    },
    validations: {
        userData: {
            email: {
                required,
                email
            },

            name: {
                required,
                minLength: minLength(3)
            },
            phone_number: {
                required,
                numeric,
                minLength: minLength(11),
                maxLength: maxLength(11)
            },
            password: {
                required,
                strongPassword(password){
                    return /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.{8,})/.test(password)

                }
            },
            confirm_password: {
                required,
                sameAsPassword: sameAs('password')
            }
        }

    },

   components: {
            'app-header': Header
        },

    methods: {

        // delayTouch($v) {
        //     $v.$reset()
        //     if (touchMap.has($v)) {
        //         clearTimeout(touchMap.get($v))
        //     }
        //     touchMap.set($v, setTimeout($v.$touch, 1000))
        // },

        // userAccount() {
        //     this.$router.push({
        //         path: 'userAccount'
        //     })
        // },

        signIn() {
            this.$router.push({
                path: 'SignIn'
            })
        },


        createUser() {
            this.$v.$touch()
            if (this.$v.$invalid) {
                this.submitStatus = 'ERROR'
            } else {
                this.disabled = true;
                this.loader = true;
                // alert("hello world")
                axios.post("/users/register", this.userData, {
                        headers: {
                            'content-type': 'application/json',
                            'Accept': 'application/json'
                        }
                    })
                    .then(response => {
                        this.loader = false;
                        //console.log(this.userData)
                        alert("response", response.data.msg)
                        //console.log(this.userData)
                    })
                    .catch(error => {
                        this.loader = false;
                        this.disabled = false;
                        alert(error.response.data.errors || error.response.data.msg)
                        // this.errors = error.response.data.errors;
                        // alert(this.errors)
                        // console.log(error)
                    })
            }
        }
    }

}


</script>

<style scoped src="../assets/styles/SignUp.css" ></style>